<template>
  <div class="class-collapse">
    <el-collapse >
      <el-collapse-item style="margin: 0px 20px" v-for="(value, name, index) in valueS">
        <template #title>
          <div class="location-header" >
            <div class="location-header-title">
              <span class="iconfont icon-iojiqun"></span>
              <span>location-{{name}}</span>
            </div>
          </div>
        </template>
        <div class="config-form-content mt5">
          <div class="config-form-body ml15" v-if="value">
            <el-form label-width="150px">
              <el-row >
                <el-col :span="7">
                  <el-form-item  :label="'location'">
                    <el-input v-model="value.location" disabled style="width: 100%"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item  :label="'limit_rate'" style="margin-left: 12%">
                    <el-input v-model="value.limit_rate" style="width: 100%"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item  :label="'limit_rate_after'" style="margin-left: 12%">
                    <el-input v-model="value.limit_rate_after" style="width: 100%"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="1">
                  <el-form-item style="width:400px" />
                </el-col>
              </el-row>

              <el-row>
                <el-col :span="7">
                  <el-form-item  :label="'limit_conn_dry_run'">
                    <el-select v-model="value.limit_conn_dry_run" style="width: 100%" @change="selectDryRunChange(value)">
                      <el-option
                          v-for="val in valueOnOff"
                          :key="val.value"
                          :label="val.label"
                          :value="val.value"
                      />
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item  :label="'limit_conn_log_level'" style="margin-left: 12%">
                    <el-input v-model="value.limit_conn_log_level" style="width: 100%"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item  :label="'limit_conn_status'" style="margin-left: 12%">
                    <el-input v-model="value.limit_conn_status" style="width: 100%"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="1">
                  <el-form-item style="width:400px" />
                </el-col>
              </el-row>

              <el-row>
                <el-col :span="7">
                  <el-form-item  :label="'limit_req_dry_run'">
                    <el-select v-model="value.limit_req_dry_run" style="width: 100%" @change="selectDryRunChange(value)">
                      <el-option
                          v-for="val in valueOnOff"
                          :key="val.value"
                          :label="val.label"
                          :value="val.value"
                      />
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item  :label="'limit_req_log_level'" style="margin-left: 12%">
                    <el-input v-model="value.limit_req_log_level" style="width: 100%"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item  :label="'limit_req_status'" style="margin-left: 12%">
                    <el-input v-model="value.limit_req_status" style="width: 100%"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="1">
                  <el-form-item style="width:400px" />
                </el-col>
              </el-row>

              <el-card style="padding-left: 1px">
                <div class="location-header" >
                  <div class="location-header-title">
                    <span class="iconfont icon-iojiqun"></span>
                    <span>limit_conns:</span>
                  </div>
                </div>
                <el-row v-if="value.limit_conns && value.limit_conns.length>0">
                  <el-form-item  v-for="(item, idx) in value.limit_conns">
                    <el-form>
                      <div style="display: block">
                        <div style="display: flex">
                          <div  v-for="(itemIp, key, id) in item">
                            <el-form-item  :label="key">
                              <el-input v-model="item[key]" style="width: 70%">
                              </el-input>
                            </el-form-item>
                          </div>
                          <el-form-item>
                            <el-icon class="tree-node-btn-add" @click.stop="addNode(value.limit_conns, true)"><CirclePlus /></el-icon>
                            <el-icon class="tree-node-btn-del" @click.stop="removeNode(value.limit_conns,idx, value)"><Remove /></el-icon>
                          </el-form-item>
                        </div>
                      </div>
                    </el-form>
                  </el-form-item>
                </el-row>
                <el-row v-else>
                  <div style="display: flex">
                    <span style="margin-left:14px;width: 90px">limit_conns:</span>
                    <el-icon class="tree-node-btn-add" @click.stop="addNode(value, false)"><CirclePlus /></el-icon>
                  </div>
                </el-row>
              </el-card>

              <el-card style="padding-left: 1px; margin-top: 5px">
                <div class="location-header" >
                  <div class="location-header-title">
                    <span class="iconfont icon-iojiqun"></span>
                    <span>limit_reqs:</span>
                  </div>
                </div>
                <el-row v-if="value.limit_reqs && value.limit_reqs.length>0">
                  <el-form-item  v-for="(item, idx) in value.limit_reqs">
                    <el-form>
                      <div style="display: block">
                        <div style="display: flex">
                          <div  v-for="(itemIp, key, id) in item">
                            <el-form-item  :label="key">
                              <el-input v-model="item[key]" style="width: 70%">
                              </el-input>
                            </el-form-item>
                          </div>
                          <el-form-item>
                            <el-icon class="tree-node-btn-add" @click.stop="addReqNode(value.limit_reqs, true)"><CirclePlus /></el-icon>
                            <el-icon class="tree-node-btn-del" @click.stop="removeReqNode(value.limit_reqs,idx, value)"><Remove /></el-icon>
                          </el-form-item>
                        </div>
                      </div>
                    </el-form>
                  </el-form-item>
                </el-row>
                <el-row v-else>
                  <div style="display: flex">
                    <span style="margin-left:14px;width: 90px">limit_reqs:</span>
                    <el-icon class="tree-node-btn-add" @click.stop="addReqNode(value, false)"><CirclePlus /></el-icon>
                  </div>
                </el-row>
              </el-card>

              <div v-if="value.locations">
                <Locations :value-s="value.locations"></Locations>
              </div>
            </el-form>
          </div>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>

</template>

<script setup>

import {getCurrentInstance, ref} from "vue";
import Locations from './httpDynLimitLocation'
const {proxy} = getCurrentInstance()

const props = defineProps({
  nameS: {
    type: String,
    default: '',
  },
  valueS: {
    type: Object,
    default: {}
  }
})

const valueOnOff = ref([
  {
    label: 'on',
    value: 'on'
  },
  {
    label: 'off',
    value: 'off'
  }
]);

function addNode(value, bl){
  if(bl){
    let child = {zone: 'zone', conn: '100'}
    value.push(child)
  }else{
    console.log("'''''''limit_conns''''''''")
    console.log(value)
    let child = [{zone: 'zone', conn: '100'}]
    value["limit_conns"] = child
  }
}
function removeNode(value, idx, locData){
  console.log(idx)
  if(value){
    value.splice(idx, 1)
  }
  if(value.length <= 0){
    Reflect.deleteProperty(locData, 'limit_conns')
  }
}

function addReqNode(value, bl){
  if(bl){
    let child = {zone: 'zone', burst: '100', delay: '1000'}
    value.push(child)
  }else{
    console.log("''''''''limit_reqs'''''''")
    console.log(value)
    let child = [{zone: 'zone', burst: '100', delay: '1000'}]
    value["limit_reqs"] = child
  }
}
function removeReqNode(value, idx, locData){
  console.log(idx)
  if(value){
    value.splice(idx, 1)
  }
  if(value.length <= 0){
    Reflect.deleteProperty(locData, 'limit_reqs')
  }
}


</script>

<style lang="scss" scoped>
.demo-tabs > .el-tabs__content {
  padding: 32px;
  color: #6b778c;
  font-size: 32px;
  font-weight: 600;
}



</style>
