//处理从服务器获取的配置
export function handleAttrConfigs(defaultConfigs, list){
  defaultConfigs.forEach(item=>{
    let find = list.find(t=>t.key===item.key);
    if(find){
      item.enable=find.enable;
      item.id = find.id;
      item.value=[];  //已选属性
      item.selectedValue=[];  //待选属性
      item.formProps={};
      item.attrList.forEach(m=>{
        let defaultAttr= find.value.find(x=>x.key===m.prop);
        if(defaultAttr){
          let finds = find.value.filter(x=>x.key===m.prop);
          for(let i = 0;i<finds.length;i++){
            let copyItem = clone(m);
            copyItem['hidden']=finds[i].hidden;
            copyItem['readonly']=finds[i].readonly;
            copyItem['instanceId']=finds[i].instanceId;
            //深拷贝会丢失校验正则表达式，要重新赋值
            copyItem.rule = m.rule;
            if(item.formProps.hasOwnProperty(m.prop)){
              let newProp=m.prop+'@'+i;
              copyItem.prop=newProp;
              item.formProps[newProp]=finds[i].value;
              item.value.push(copyItem);
            }else {
              item.formProps[m.prop]=finds[i].value;
              item.value.push(copyItem);
            }
          }
          if(m.repeated==='Y'){
            //如果是可重复，待选属性也要添加
            item.selectedValue.push(clone(m));
          }
        }else {
          let copyItem = clone(m);
          //深拷贝会丢失校验正则表达式，要重新赋值
          copyItem.rule = m.rule;
          item.selectedValue.push(copyItem);
        }
      })
    }
    // else {
    //   item.enable=false;
    // }
  })
}

//处理配置保存数据
export function handleSaveAttrConfigs(configs, list){
  configs.forEach(t=>{
    let item = {
      key: t.key,
      id: t.id,
      enable: t.enable,
      value: []
    }
    //按默认排序保存
    t.attrList.forEach(m=>{
      for (let key in t.formProps) {
        //为了处理有属性重复的情况，必须重复遍历
        if(key.split('@')[0]===m.prop){
          let find = t.value.find(n=>n.prop===key);
          item.value.push({
            key: m.prop,
            value: t.formProps[key],
            hidden: !!find.hidden,
            readonly: !!find.readonly,
            instanceId: find.instanceId
          })
        }
      }
    })
    list.push(item);
  })
}

//重置
export function resetAttrConfigs(configs){
  configs.forEach(t=>{
    t.id=undefined;
    t.enable=t.canDel==='Y';
    t.value=[];  //已选属性
    t.selectedValue=[];  //待选属性
    t.formProps={};
    t.attrList.forEach(m=>{
      if(m.required==='Y'){
        //默认显示必输入项
        t.formProps[m.prop]=m.defaultValue;
        t.value.push(m);
      }else {
        //待选择项
        t.selectedValue.push(m);
      }
    })
  })
}
