<template>
  <el-tabs
      v-model="editableTabsValue"
      type="card"
      editable
      class="demo-tabs"
      ref="tabViewRef"
      @tab-remove="removeTab"
  >
    <el-tab-pane
        v-for="item in editableTabs"
        :key="item.name"
        :label="item.title"
        :name="item.name"
    >

      <TabContentView class="tabContentViewClass" :tabViewTitle="props.tabTitle" :tab-content="props.tabContent"></TabContentView>


    </el-tab-pane>
  </el-tabs>
</template>
<script setup>
import {onMounted, ref, watch} from 'vue'
import TabContentView from './tabContentView'

const props = defineProps({
  tabTitle: {
    type: String,
    default: '',
  },
  tabContent: {
    type: Object,
    default: {}
  }
})

let tabIndex = 1
const editableTabsValue = ref('1')
const editableTabs = ref([
  {
    title: '无',
    name: '1',
    content: [],
  }
])

function findTabsView(title, content){
  let isFind = false
  editableTabs.value.forEach(t=>{
    if(t.title.indexOf(title) != -1){
      editableTabsValue.value = t.name
      isFind = true
    }
  })
  if(!isFind){
    handleAddTabs(title, content)
  }
}

function handleAddTabs(title, content){
  const newTabName = `${++tabIndex}`
  editableTabs.value.push({
    title: title,
    name: newTabName,
    content: content,
  })
  editableTabsValue.value = newTabName
}

function removeTab(targetName){
  console.log("----removeTab-------")
  console.log(targetName)
  const tabs = editableTabs.value
  let activeName = editableTabsValue.value
  if (activeName === targetName) {
    tabs.forEach((tab, index) => {
      if (tab.name === targetName) {
        const nextTab = tabs[index + 1] || tabs[index - 1]
        if (nextTab) {
          activeName = nextTab.name
        }
        if(tab.title === props.tabTitle){
          console.log("tab.title === props.tabTitle")
        }
      }
    })
  }

  editableTabsValue.value = activeName
  editableTabs.value = tabs.filter((tab) => tab.name !== targetName)
}

function handleTabsEdit(targetName, action){
  console.log("----handleTabsEdit-------")
  console.log(targetName)
  console.log(action)
  if (action === 'add') {
    const newTabName = `${++tabIndex}`
    editableTabs.value.push({
      title: 'New Tab',
      name: newTabName,
      content: 'New Tab content',
    })
    editableTabsValue.value = newTabName
  } else if (action === 'remove') {
    const tabs = editableTabs.value
    let activeName = editableTabsValue.value
    if (activeName === targetName) {
      tabs.forEach((tab, index) => {
        if (tab.name === targetName) {
          const nextTab = tabs[index + 1] || tabs[index - 1]
          if (nextTab) {
            activeName = nextTab.name
          }
        }
      })
    }

    editableTabsValue.value = activeName
    editableTabs.value = tabs.filter((tab) => tab.name !== targetName)
  }
}

watch(()=>props.tabTitle, (value)=>{
  console.log(".......watch......")
  console.log(props.tabTitle)
  if(value !== undefined){
    findTabsView(props.tabTitle, props.tabContent)
  }
},{ immediate: true })

onMounted(()=>{
  editableTabs.value = []
  tabIndex = 0;
  editableTabsValue.value = tabIndex
});

</script>
<style>
.demo-tabs > .el-tabs__content {
  /*padding: 32px;*/
  color: #6b778c;
  font-size: 32px;
  font-weight: 600;
}
.tabContentViewClass{
  margin-right: 205px;
}
</style>
