    <div class="container-header">
      <span class="container-header-icon iconfont icon-jiqun"></span>
      <div>
        <div class="container-header-title">{{tabViewTitle}}模块设置</div>
        <div class="container-header-des">对NGINX相关模块进行动态配置</div>
      </div>
    </div>
<!--    <el-scrollbar style="height: calc(100vh - 220px);">-->
    <div v-for="(value, name, index) in tabContent">
      <div v-if="tabViewTitle === 'http_split_clients_2' && name === 'http'">
        <HttpSplit :value-s="value"></HttpSplit>
      </div>
      <div v-else-if="tabViewTitle === 'http_dyn_bwlist'">
        <DynBw :value-s="value"></DynBw>
      </div>
      <div v-else-if="tabViewTitle === 'otel' && name === 'servers'">
        <OtelIndex :value-s="value" :name-s="tabViewTitle"></OtelIndex>
      </div>
      <div v-else-if="tabViewTitle === 'otel_webserver' && name === 'servers'">
        <OtelIndex :value-s="value" :name-s="tabViewTitle"></OtelIndex>
      </div>
      <div v-else-if="tabViewTitle === 'http_ssl' && name === 'servers'">
