      <HttpLogIndex v-else :value-s="tabContent.servers" :value-f="tabCont"></HttpLogIndex>
    </div>
    <div v-else-if="tabViewTitle === 'http_dyn_limit'">
      <HttpDynLimit :value-s="tabContent.servers" :value-f="tabContent.limit_rps"></HttpDynLimit>
