<template>
  <div class="container-body mt10">
    <el-form ref="addFormRef" :inline="true" style="height: 18px" @submit.native.prevent>
      <el-form-item></el-form-item>
      <el-form-item class="el-form-item__right">
        <el-tooltip content="保存" effect="dark" placement="top">
          <el-icon class="icon-primary mr10 cursor-pointer" @click="saveFileContent">
            <span class="iconfont icon-Save"></span>
          </el-icon>
        </el-tooltip>
      </el-form-item>
    </el-form>
  </div>
    <el-scrollbar style="height: calc(100vh - 300px);">
      <div class="class-collapse" >
        <el-collapse v-model="activeAccessLogFormatNames">
          <el-collapse-item name="accessLogFormat"  style="margin: 0px 20px">
            <template #title>
              <div class="location-header" >
                <div style="display: flex">
                  <div class="location-header-title">
                    <span class="iconfont icon-iojiqun"></span>
                    <span>accessLogFormats:({{valueF.length}}条)</span>
                  </div>
                  <div class="ml10 mt5">
                    <el-icon v-if="valueF.length <=0" class="tree-node-btn-add" @click.stop="addFormat(valueF, false)"><CirclePlus /></el-icon>
                  </div>
                </div>
              </div>
            </template>
            <div class="config-form-content mt5" v-for="(value, index) in valueF">
              <el-row>
                <div class="config-form-body ml15" v-if="value">
                  <el-form>
                    <el-col :span="10">
                      <el-form-item  :label="'name'">
                        <el-input v-model="value.name" style="width: 100%"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="4">
                      <el-form-item style="width:400px">
                      </el-form-item>
                    </el-col>
                    <el-col :span="10">
                      <el-form-item  :label="'format'">
                        <el-input v-model="value.format" style="width: 100%"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="1">
                      <el-form-item style="width:400px" />
                    </el-col>
                    <el-col :span="2">
                      <el-form-item class="pt5">
                        <el-icon class="tree-node-btn-add" @click.stop="addFormat(valueF, true)"><CirclePlus /></el-icon>
                        <el-icon v-if="removeList.indexOf(index) > -1" class="tree-node-btn-del" @click.stop="removeFormat(valueF,index)"><Remove /></el-icon>
                      </el-form-item>
                    </el-col>
                  </el-form>
                </div>
              </el-row>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <div class="class-collapse" v-for="(value, name, index) in valueS">
        <el-collapse v-model="activeNames" @change="handleChange">
          <el-collapse-item  style="margin: 0px 20px">
            <template #title>
              <div class="location-header" >
                <div class="location-header-title">
                  <span class="iconfont icon-iojiqun"></span>
                  <span>server-{{name}}</span>
                </div>
              </div>
            </template>
            <div class="config-form-content mt5">
              <el-row>
                <div class="config-form-body ml15" v-if="value">
                  <el-form>
                    <el-col :span="10">
                      <el-form-item  :label="'listens'" v-for="(item, idx) in value.listens">
                        <el-input v-model="value.listens[idx]" disabled style="width: 100%"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="4">
                      <el-form-item style="width:400px">
                      </el-form-item>
                    </el-col>
                    <el-col :span="10">
                      <el-form-item  :label="'serverNames'" v-for="(item, idx) in value.serverNames">
                        <el-input v-model="value.serverNames[idx]" disabled style="width: 100%"></el-input>
                      </el-form-item>
                    </el-col>
                  </el-form>
                </div>
              </el-row>
              <el-row>
                <HttpLogLocation :value-s="value.locations"></HttpLogLocation>
              </el-row>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
  </el-scrollbar>
</template>

<script setup>

import {getCurrentInstance, ref} from "vue";
import HttpLogLocation from './httpLogLocation'
import {postModuleData} from "@/components/Api/api";
const {proxy} = getCurrentInstance();

const props = defineProps({
  nameS: {
    type: String,
    default: '',
  },
  valueS: {
    type: Object,
    default: {}
  },
  valueF: {
    type: Array,
    default: []
  }
})

const activeNames = ref('1')
const activeAccessLogFormatNames = ref('accessLogFormat')
const removeList = ref([])

function addFormat(value, bl){
  console.log(value)
  let child = {name: 'test', format: '$remote_addr - $remote_user [$time_local] "$request" $status $body_bytes_sent "$http_referer" "$http_user_agent"'}
  value.push(child)
  // if(bl){
  //   let child = {name: 'test', format: '$remote_addr - $remote_user [$time_local] "$request" $status $body_bytes_sent "$http_referer" "$http_user_agent"'}
  //   value.push(child)
  // }else{
  //   console.log("'''''''''''''''")
  //   console.log(value)
  //   console.log(value.length)
  //   let child = {name: 'test', format: '$remote_addr - $remote_user [$time_local] "$request" $status $body_bytes_sent "$http_referer" "$http_user_agent"'}
  //   value.push(child)
  //   console.log("'''''''''''''''")
  //   console.log(value)
  // }

  //新添加又没有保存的的允许删除
  if(value.length>0) {
    removeList.value.push(value.length - 1)
  }else{
    removeList.value.push(0)
  }
  console.log(removeList.value)
}
function removeFormat(value, idx){
  console.log(idx)
  if(value){
    value.splice(idx, 1)
  }
  // console.log(value)
}

function saveFileContent(){
  console.log(".....savex")
  console.log(props.valueS)
  let body = {}
  body['servers'] = props.valueS
  body['accessLogFormats'] = props.valueF
  console.log(body)
  putModuleHttpSplit('http_log', body)
}

function putModuleHttpSplit(module,body) {
  postModuleData(module,body).then(res=>{
    console.log(res);
    proxy.$message.info("修改成功")
    removeList.value = []
  })
}
</script>

<style lang="scss" scoped>

.class-collapse{
  background-color: #FFFFFF;
  width: 100%;
}

.config-form{
  border: 1px solid var(--el-border-color-light);
  border-radius: 3px;
  padding: 10px;
  &-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-left{
      display: flex;
      align-items: center;
      &__add{
        color: var(--el-color-primary) !important;
        cursor: pointer;
      }
      &__icon{
        font-size: 18px;
        margin-right: 2px;
      }
    }
  }
  &-body{
    //display: flex;
    flex-wrap: wrap;
    .el-form-item{
      //margin-bottom: 15px;
    }
    &-content{
      display: flex;
      align-items: center;
      justify-content: space-between;
      :deep(.el-input-number .el-input__inner){
        text-align: left !important;
      }
    }
  }
  &-content{
    padding: 10px 10px 0px 10px;
    //border: 1px solid var(--el-border-color-light);
    border-radius: 5px;
    background: #F7F8FA;
  }
}



</style>
