<template>
  <div class="class-collapse">
    <el-collapse >
      <el-collapse-item style="margin: 0px 20px" v-for="(value, name, index) in valueS">
        <template #title>
          <div class="location-header" >
            <div class="location-header-title">
              <span class="iconfont icon-iojiqun"></span>
              <span>location-{{name}}</span>
            </div>
          </div>
        </template>
        <div class="config-form-content mt5">
          <div class="config-form-body ml15" v-if="value">
            <el-form>
              <el-row >
                <el-form-item  :label="'location'">
                  <el-input v-model="value.location" disabled style="width: 100%"></el-input>
                </el-form-item>
              </el-row>
              <el-card style="padding-left: 10px">
                <el-row v-if="value.accessIpv4">
                  <el-form-item  :label="'accessIpv4: '" v-for="(item, idx) in value.accessIpv4">
                    <el-form>
                      <div style="display: block">
                        <div style="display: flex">
                          <div  v-for="(itemIp, key, id) in item">
                            <el-form-item  :label="key">
                              <div v-if="key==='rule'" >
                                <el-select v-model="item[key]" style="width: 50%">
                                  <el-option
                                      v-for="val in valueList"
                                      :key="val.value"
                                      :label="val.label"
                                      :value="val.value"
                                  />
                                </el-select>
                              </div>
                              <div v-else>
                                <el-input v-model="item[key]" style="width: 70%"
                                          @change="((val)=>{changeStatus(val)})">
                                </el-input>
                              </div>
                            </el-form-item>
                          </div>
                          <el-form-item>
                            <el-icon class="tree-node-btn-add" @click.stop="addNode(value.accessIpv4, true)"><CirclePlus /></el-icon>
                            <el-icon class="tree-node-btn-del" @click.stop="removeNode(value.accessIpv4,idx, value)"><Remove /></el-icon>
                          </el-form-item>
                        </div>
                      </div>
                    </el-form>
                  </el-form-item>
                </el-row>
                <el-row v-else>
                  <div style="display: flex">
                    <span style="width: 90px">accessIpv4:</span>
                    <el-icon class="tree-node-btn-add" @click.stop="addNode(value, false)"><CirclePlus /></el-icon>
                  </div>
                </el-row>
              </el-card>
              <div v-if="value.locations">
                <Locations :value-s="value.locations"></Locations>
              </div>
            </el-form>
          </div>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>

</template>

<script setup>
import {getCurrentInstance, ref} from "vue";
import Locations from './locations'
const {proxy} = getCurrentInstance()

const props = defineProps({
  nameS: {
    type: String,
    default: '',
  },
  valueS: {
    type: Object,
    default: {}
  }
})

const valueList = ref([
  {
    label: 'deny',
    value: 'deny'
  },
  {
    label: 'allow',
    value: 'allow'
  }
]);

function addNode(value, bl){
  if(bl){
    let child = {rule: 'deny', addr: '0.0.0.0', mask: '0.0.0.0'}
    value.push(child)
  }else{
    console.log("'''''''''''''''")
    console.log(value)
    let child = [{rule: 'deny', addr: '0.0.0.0', mask: '0.0.0.0'}]
    value["accessIpv4"] = child
  }
}
function removeNode(value, idx, locData){
  console.log(idx)
  if(value){
    value.splice(idx, 1)
  }
  if(value.length <= 0){
    Reflect.deleteProperty(locData, 'accessIpv4')
  }
}

function changeStatus(val){
  console.log(val)
  console.log(val.length)
  let ipRegex = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/
  if (ipRegex.test(val) === false) {
    proxy.$message.error('输入IP格式有误，请重新输入！！！')
  } else {
  }
}

</script>

<style lang="scss" scoped>
.demo-tabs > .el-tabs__content {
  padding: 32px;
  color: #6b778c;
  font-size: 32px;
  font-weight: 600;
}



</style>
