<script setup>
import {ref} from "vue";
import HttpLogLocation from './httpLogLocation'
import HttpAccessLogOn from './accessLogOn'

const props = defineProps({
  nameS: {
    type: String,
    default: '',
  },
  valueS: {
    type: Object,
    default: {}
  }
})
const valueList = ref([
  {
    label: 'true',
    value: true
  },
  {
    label: 'false',
    value: false
  }
]);

// const accessLogOnactiveNames = ref([])

const radio = ref('0')

// function locationCollapseChange(){
//
// }

function radioChange(radio,value)
{
  console.log(radio)
  console.log(value)
  if(!value.accessLogs){
    value.accessLogs = []
  }
  if(radio === '2'){
    if(value.accessLogs.length == 0){
      let child = {path: 'syslog:server=192.168.40.141:514,facility=local7,tag=nginx,severity=info', formatName: 'combined'}
      value.accessLogs.push(child)
    }else{
      value.accessLogs.forEach(x=>{
        if(x.path.indexOf("syslog:server") > -1){
          console.log(".....")
          console.log(x)
        }else{
          x.path = "syslog:server=192.168.40.141:514,facility=local7,tag=nginx,severity=info"
          x.formatName = "combined"
        }
      })

    }
  }else if(radio === '1'){
    if(value.accessLogs.length == 0){
      let child = {path: 'logs/access.log', formatName: 'combined'}
      value.accessLogs.push(child)
    }else{
      value.accessLogs.forEach(x=>{
          x.path = "logs/access.log"
          x.formatName = "combined"
      })
    }
  }
}

function selectLogChange(value){
  console.log(value)
  if(value.accessLogOn) {
    if (!value.accessLogs || !value.accessLogs.length) {
      let child = {path: 'logs/access.log', formatName: 'combined'}
      value.accessLogs = []
      value.accessLogs.push(child)
    }
  }
  console.log(value)
}

</script>

