<template>
  <el-scrollbar wrap-class="scrollbar-wrapper">
    <el-menu
        :default-active="activeMenu"
        class="el-menu-vertical-demo"
        @select="selectActive"
    >
        <div v-for="(route, index) in sidebarRouters">
          <el-menu-item :index="index" v-if="route != 'njt_http_kv_module'">
            <el-icon><setting /></el-icon>
            <span>{{route}}</span>
          </el-menu-item>
        </div>
    </el-menu>
  </el-scrollbar>

</template>

<script setup>
import {
  Document,
  Menu as IconMenu,
  Location,
  Setting,
} from '@element-plus/icons-vue'

const {proxy} = getCurrentInstance()
import {getCurrentInstance, onMounted, reactive, ref, watch} from "vue";
import {getModuleList} from '@/components/Api/api'
const sidebarRouters = ref([]);

const activeMenu = ref(1);

function selectActive(index){
  activeMenu.value = index
  console.log(activeMenu.value)
  console.log(sidebarRouters.value[activeMenu.value])
  console.log("emit..............")
  proxy.$emit('tabViewName',sidebarRouters.value[activeMenu.value])
}

function getModuleLst() {
  console.log("----getModuleLst----")
  getModuleList().then(res=>{
    console.log(res);
    sidebarRouters.value = res
    if(sidebarRouters.value.length > 1){
      proxy.$emit('tabViewName',sidebarRouters.value[1])
    }
  })
}

// watch(()=>sidebarRouters.value, (value)=>{
//   console.log(".............")
//   console.log(sidebarRouters.value)
//   sidebarRouters.value = sidebarRoutersTp.value
// },{ immediate: true })

onMounted(()=>{
  getModuleLst()
});

</script>

<style lang="scss" scoped>

:deep(.el-menu){
  background-color: transparent !important;
}
:deep(.el-menu-item), :deep(.el-sub-menu__title){
  font-size: 16px;
  //padding: 30px !important;
  padding-top: 30px;
  padding-bottom: 30px;
}
:deep(.el-menu-item.is-active){
  border-right: 4px solid var(--el-color-primary)!important;
  //background-color: var(--el-color-primary-light-9)!important;
  background-color: rgba(0, 0, 0, 0.08) !important;
  font-weight: bold;
}
:deep(.el-menu-item:hover){
  background-color: rgba(0, 0, 0, 0.08) !important;
}

</style>
