<template>
  <div class="form-body">
    <el-form  label-width="160px">
      <el-row :gutter="5" v-for="(value,key,index) in items">
        <el-col :span="20">
          <el-form-item>
            <template #label>
              <el-button disabled style="width: 100%;font-weight: 600">{{key}}</el-button>
            </template>
            <el-input-number v-model="items[key]" :precision="0" :step="5" :max="100" style="width: 120px"/>
            <span class="ml5">%</span>
          </el-form-item>
        </el-col>
<!--        <el-col :span="4">-->
<!--          <div class="form-body-icons">-->
<!--            <el-icon class="form-body-icons__add mt5" :size="20" @click="addItem()"><CirclePlus /></el-icon>-->
<!--            <el-icon class="form-body-icons__reduce mt5" :size="20" @click="reduceItem(index)" v-if="items.length>1"><Remove /></el-icon>-->
<!--          </div>-->
<!--        </el-col>-->
      </el-row>
    </el-form>
  </div>
</template>

<script setup>
import {ref} from "vue";

const props = defineProps({
  items: {
    type: Object,
    default: {},
  },
  selectItems: {
    type: Array,
    default: [],
  },
  keyName: {
    type: String,
    default: 'key'
  },
  canAdd: {
    type: Boolean,
    default: true
  }
})

const tabContents = ref({})

function setTabContents(content){
  tabContents.value = content
}

function addItem(){
  props.items.push({value: ''})
}
function reduceItem(index){
  props.items.splice(index, 1)
}
</script>

<style lang="scss" scoped>
.form-body{
  width: 100%;
  &-icons{
    display: flex;
    align-items: center;
    &__add{
      color: var(--el-color-primary);
      cursor: pointer;
    }
    &__reduce{
      color: #ed5565;
      margin-left: 5px;
      cursor: pointer;
    }
  }
}
</style>
