import request from "@/store/utils/request";

export function getModuleList() {
    return request({
        url: `/2/config`,
        method: 'get'
    })
}

export function getModuleContentData(module) {
    return request({
        url: `/2/config/${module}`,
        method: 'get'
    })
}

export function postModuleData(module,body){
    return request({
        url: `/2/config/${module}`,
        method: 'put',
        data: body
    })
}
