<template>
  <div class="config-form-content mt5">
    <div class="config-form-body ml15" v-if="value">
      <el-form>
        <el-row>
          <el-col :span="10">
            <el-form-item  :label="'location'">
              <el-input v-model="value.location" disabled style="width: 100%"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <el-form-item style="width:400px">
            </el-form-item>
          </el-col>
          <el-col :span="10" v-if="value.accessLogOn != 'undefined'">
            <el-form-item  :label="'accessLogOn: '">
              <el-select v-model="value.accessLogOn" style="width: 100%" @change="selectLogChange(value)">
                <el-option
                    v-for="val in valueList"
                    :key="val.value"
                    :label="val.label"
                    :value="val.value"
                />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <!--              <HttpAccessLogOn :access-logs="value.accessLogs"></HttpAccessLogOn>-->
        <div v-if="value.accessLogs" class="class-collapse">
          <el-collapse v-model="activeNames" >
            <el-collapse-item style="margin: 0px 20px" name="1">
              <template #title>
                <div class="location-header" >
                  <div class="location-header-title">
                    <span class="iconfont icon-iojiqun"></span>
                    <span>accessLogs:</span>
                    <el-radio-group v-model="radio" class="ml20"  @click.stop="" @change="radioChange(radio,value)">
                      <el-radio label="1" size="small">acccesslog</el-radio>
                      <el-radio label="2" size="small">syslog</el-radio>
                    </el-radio-group>
                  </div>
                </div>
              </template>
              <div class="config-form-content mt5"  v-for="(valueL, indexL) in value.accessLogs">
                <div class="config-form-body ml15" v-if="valueL">
                  <el-form>
                    <el-row>
                      <el-col :span="10">
                        <el-form-item  :label="'path'">
                          <el-input v-model="valueL.path" style="width: 100%"></el-input>
                        </el-form-item>
                      </el-col>
                      <el-col :span="4">
                        <el-form-item style="width:400px">
                        </el-form-item>
                      </el-col>
                      <el-col :span="10" >
                        <el-form-item  :label="'formatName: '">
                          <el-input v-model="valueL.formatName" style="width: 100%"></el-input>
                        </el-form-item>
                      </el-col>
                    </el-row>
                  </el-form>
                </div>
              </div>
            </el-collapse-item>
          </el-collapse>
        </div>
        <div v-if="value.locations">
          <HttpLogLocation :value-s="value.locations"></HttpLogLocation>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script setup>
import {ref} from "vue";
import HttpLogLocation from './httpLogLocation'
const props = defineProps({
  value: {
    type: Array,
    default: []
  }
})
const valueList = ref([
  {
    label: 'true',
    value: true
  },
  {
    label: 'false',
    value: false
  }
]);

const radio = ref('0')

const activeNames = ref('1')

function radioChange(radio,value)
{
  console.log(radio)
  console.log(value)
  if(!value.accessLogs){
    value.accessLogs = []
  }
  if(radio === '2'){
    if(value.accessLogs.length == 0){
      let child = {path: 'syslog:server=192.168.40.141:514,facility=local7,tag=nginx,severity=info', formatName: 'combined'}
      value.accessLogs.push(child)
    }else{
      value.accessLogs.forEach(x=>{
        if(x.path.indexOf("syslog:server") > -1){
          console.log(".....")
          console.log(x)
        }else{
          x.path = "syslog:server=192.168.40.141:514,facility=local7,tag=nginx,severity=info"
          // x.formatName = "combined"
        }
      })

    }
  }else if(radio === '1'){
    if(value.accessLogs.length == 0){
      let child = {path: 'logs/access.log', formatName: 'combined'}
      value.accessLogs.push(child)
    }else{
      value.accessLogs.forEach(x=>{
        x.path = "logs/access.log"
        // x.formatName = "combined"
      })
    }
  }
}

function selectLogChange(value){
  console.log(value)
  if(value.accessLogOn) {
    if (!value.accessLogs || !value.accessLogs.length) {
      let child = {path: 'logs/access.log', formatName: 'combined'}
      value.accessLogs = []
      value.accessLogs.push(child)
    }
  }
  console.log(value)
}
</script>

<style lang="scss" scoped>

</style>
